// Инструменты
import React from 'react';
import connect from '@vkontakte/vk-connect';
// VKUI
import View from '@vkontakte/vkui/dist/components/View/View';
import ModalRoot from '@vkontakte/vkui/dist/components/ModalRoot/ModalRoot';
import ModalPage from '@vkontakte/vkui/dist/components/ModalPage/ModalPage';
import ModalPageHeader from '@vkontakte/vkui/dist/components/ModalPageHeader/ModalPageHeader';
import HeaderButton from '@vkontakte/vkui/dist/components/HeaderButton/HeaderButton';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import Alert from '@vkontakte/vkui/dist/components/Alert/Alert';
import Link from '@vkontakte/vkui/dist/components/Link/Link';
import ScreenSpinner from '@vkontakte/vkui/dist/components/ScreenSpinner/ScreenSpinner';
import ConfigProvider from '@vkontakte/vkui/dist/components/ConfigProvider/ConfigProvider';
import {ModalCard, Avatar} from '@vkontakte/vkui';
import './panels/style.css';

// Icons
import Icon24Cancel from '@vkontakte/icons/dist/24/cancel';

// Css
import '@vkontakte/vkui/dist/vkui.css';

// Panels
import Home from './panels/home.js'

// Other
import packageJson from '../package.json';

// Поддержка бюджетных устройств

class App extends React.Component {

	constructor(props) {
		super(props);

		this.state = {
			error: "",
			object_error: [],
			activeModal: null,
			modalHistory: [],
			text: '',
			snackbar: null,
			image_url: "",

			last_smoke_at: '',
			last_smoke_at_search: '',
			last_smoke_at_time_search: '',
			last_smoke_at_time: '',
			last_smoke_at_date: '',
			last_smoke_at_date_search: '',
			years_of_smoking: '',
			years_of_smoking_search: '',
			months_of_smoking: '',
			months_of_smoking_search: '',
			cigarettes_per_day: '',
			cigarettes_per_day_search: '',
			pack_cost: '',
			pack_cost_search: '',
			tar_per_cigarette: '',
			tar_per_cigarette_search: '',
			nicotine_per_cigarette: '',
			nicotine_per_cigarette_search: '',
			stat: {
                not_smoked_cigarettes: 0,
                not_smoked_days: 0,
                money_saved: 0,
                not_received_tar: 0,
                not_received_nicotine: 0,
                time_saved_hours: 0,
                life_extension_days: 0,
            },
            harm_stat: {
                smoked_days: 0,
                smoked_cigarettes: 0,
                money_spent: 0,
                hours_spent: 0,
                received_tar: 0,
                received_nicotine: 0,

                cigarette_butts_weight: 0,
                netflix_sub_months: 0,
                marathons: 0,
                rabbits: 0
            },
            activePanel: 'home',
            history: ['home'],
            is_support: true,
            backend_url: 'https://smit.poprosche.ru/p/ne-kuru-backend/public/',
			health_list: [],
			object_error: {
				cigarettes_per_day: "",
			}
		};


		this.modalBack = () => {
			this.setActiveModal(this.state.modalHistory[this.state.modalHistory.length - 2]);
		};
	}

	setActiveModal(activeModal) {
		activeModal = activeModal || null;
		let modalHistory = this.state.modalHistory ? [...this.state.modalHistory] : [];

		if (activeModal === null) {
			modalHistory = [];
		} else if (modalHistory.indexOf(activeModal) !== -1) {
			modalHistory = modalHistory.splice(0, modalHistory.indexOf(activeModal) + 1);
		} else {
			modalHistory.push(activeModal);
		}

		this.setState({
			activeModal,
			modalHistory
		});
	};
		
	render() {
		const modal = (
			<ModalRoot activeModal={this.state.activeModal}>
                <ModalCard
					id="freshmen"
					onClose={this.modalBack}
					icon={
                        <Avatar size={72} src="https://sun9-24.userapi.com/c858328/v858328522/119d63/kbl3IvOg-Uo.png" />
                    }
					title="Фрешмен"
                    caption="Вы нашли 10 интересных записей"
                    actions={[
                        {
                            title: <div className="color-blue">Готово</div>,
                            type: 'secondary',
                            action: this.modalBack
                        }
                    ]}
				/>
                <ModalCard
					id="profi"
					onClose={this.modalBack}
					icon={
                        <Avatar size={72} src="https://sun9-24.userapi.com/c858328/v858328522/119d63/kbl3IvOg-Uo.png" />
                    }
					title="Профи"
                    caption="Вы нашли 100 интересных записей"
                    actions={[
                        {
                            title: <div className="color-blue">Готово</div>,
                            type: 'secondary',
                            action: this.modalBack
                        }
                    ]}
				/>

			</ModalRoot>
		);

		return (
            <ConfigProvider isWebView={true}>
            <View
            id="glav"
            activePanel={this.state.activePanel}
            modal={modal}
            onSwipeBack={this.goBack}
            history={this.state.history}
            popout={this.state.popout}
			header={false}>
				<Home id="home" this={this} connect={connect}/>
			</View>
            </ConfigProvider>
		);
	}
}

export default App;



    


